.button-join {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;

  .btn-join {
    border-radius: 15px;
    padding: 10px 20px;
    border: 1px solid blue;
    background-color: #fbfb02;
    font-weight: bold;
    font-size: 16px;
  }
}

.home-page {
  margin: 30px;
  display: flex;
  flex-direction: row;

  .about {
    text-align: center;

    h1, h3 {
      margin: 0;
    }

    h3 {
      font-style: italic;
    }

    font-family: 'Meladu', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-weight: bolder;

    .title {
      font-family: 'Comic', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
      font-weight: bolder;
    }
  }

  .logo {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
  }

  img {
    width: 300px;
  }
}

@media (max-width: 768px) {
  .home-page {
    display: flex;
    flex-direction: column;
  }
}

.image-account {
  display: flex;
  justify-content: center;
  margin: 10px 0;

  img {
    background-color: white;
    height: 120px;
    width: 120px;
    border-radius: 15px;
    border: 1px black solid;
  }
}

.registration-event {
  background-color: rgba(132, 173, 245, 0.78);
  border-radius: 15px;
  margin: 15px;

  .content {
    margin: 10px;

    .button {
      margin: 10px;

      button {
        background-color: #0f74f8;
        border-color: #0f74f8;
        color: #fff;
        border-radius: 5px;
        margin: 20px 20px;
        padding: 5px 15px;
      }
    }
  }
}

.notify-closed {
  font-size: larger;
  color: red;
  padding: 10px;
  text-align: center;
}

.button {
  text-align: center;
  margin-top: 10px;

  button {
    border: none;
    background-color: #0f74f8;
    padding: 10px;
    border-radius: 5px;
    color: white;
    font-weight: bolder;
    font-size: large;
  }

  button:hover {
    cursor: pointer;
  }

}
