.setting {
  margin: 10px;

  .hr {
    border-bottom: 1px black solid;
  }

  .setting-item {
    display: flex;
    align-items: center;
    margin: 15px;
  }

  .setting-item:hover {
    cursor: pointer;
  }

  .icon {
    margin: 0 10px;
  }

  .title-menu {
    font-weight: bold;
    font-size: 20px;
  }

  .footer {
    display: flex;
    justify-content: center;
    margin: 20px 10px;

    .image-logo {
      display: flex;

      img {
        margin-top: 25px;
        @include dimension(auto, 80px);
      }
    }
  }

  .website {
    margin: 0;
    text-align: center;

    a {
      text-decoration: none;
      color: black;
      font-style: italic;
    }
  }

  .app-info {
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 18px;

    a {
      text-decoration: none;
      color: navy;
      font-weight: bolder;
      font-style: italic;
    }
  }

}
