.bottom-navigation {
  margin-top: 70px;

  .bottom-navigation-icon {
    position: absolute;
    top: -25px;
    border-radius: 50%;
    border: 4px #0f74f8 solid;

    img {
      position: relative;
      border-radius: 50%;
      width: 65px;
      height: 60px;
      background-color: #ffffff;
    }
  }

  .bottom-navigation-icon:hover {
    cursor: pointer;
  }
}

.header-24 {
  text-align: center;
  font-weight: bold;
  font-size: 24px;
}

.header-20 {
  font-weight: bold;
  font-size: 20px;
}

.box-content {
  //background-color: rgba(255, 255, 255, 0.69);
  border-radius: 5px;
  margin: 5px 10px;
  padding: 1px;

  h1 {
    text-align: center;
    font-weight: bold;
    font-size: 24px;
  }
}

.text-italic {
  font-style: italic;
}

.text-bold {
  font-weight: bold;
}

.text-red {
  color: red;
}

.text-bold-with-small {
  font-weight: bold;
  margin-bottom: 0;
  margin-top: 5px;
}

.disc-li {
  list-style-type: disc;
  margin-left: 25px;
}

.decimal-li {
  list-style-type: decimal; /* Default gaya angka 1, 2, 3 */
  margin-left: 25px;
}

.mb-0 {
  margin-bottom: 0 !important;
}

.title-page {
  text-align: center;
  //font-weight: bolder;
  font-size: 25px;
  margin: 10px;
  font-family: 'Qureah', 'Fira Sans', fantasy;
}

.error-message {
  color: red;
  font-weight: bolder;
}

.floating {
  background-color: green;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  color: white;
  font-weight: bold;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  text-decoration: none;
  transition: ease-in 0.5s;
  position: fixed;
  right: 30px;
  bottom: 80px;
  box-shadow: 2px 4px 4px #000000;
}

#divider {
  border: 1px solid #000000;
}
