.about-page {
  .title {
    font-weight: bolder;
    font-size: x-large;
    font-family: 'Qureah', sans-serif;
    text-align: center;
  }

  .content {
    font-family: 'Qureah', 'serif';

    .title-content {
      //font-weight: bolder;
      font-size: large;
      margin-bottom: 0;
      font-family: 'Qureah', 'Fira Sans', fantasy;
    }

    .moto {
      margin-top: 0;
      font-weight: bolder;
      font-style: italic;
    }
  }

  .footer {
    display: flex;
    justify-content: center;
    margin: 10px;

    .image-logo {
      display: flex;

      img {
        @include dimension(auto, 80px);
      }
    }
  }

  .website {
    margin: 0;
    text-align: center;

    a {
      text-decoration: none;
      color: black;
      font-style: italic;
    }
  }

  .app-info {
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 18px;

    a {
      text-decoration: none;
      color: navy;
      font-weight: bolder;
      font-style: italic;
    }
  }
}
