#setting-rider {
  margin: 10px;

  .button {
    display: flex;
    justify-content: space-evenly;
    margin: 20px;

    .add-rider-button {
      background-color: #fff403;
      border: 1px solid #0f74f8;
      border-radius: 5px;
      padding: 5px 10px;
      color: black
    }

    .order-jersey-button {
      background-color: #fff403;
      border: 1px solid #0f74f8;
      border-radius: 5px;
      padding: 5px 10px;
      color: black
    }

    .disabled {
      border: 1px solid rgba(173, 173, 173, 0.75);
      background-color: grey;
      color: white;
    }
  }

  .card-rider {
    background-color: #1313ff;
    display: flex;
    border-radius: 10px;
    margin: 10px 0;
    border: 1px solid #000000;

    .image-rider {
      border-radius: 10px 0 0 10px;
      background-color: #fff403;
      width: 120px;
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        width: 100px;
        height: 120px;
        border-radius: 100%;
        margin: 10px;
      }
    }

    .data-rider {
      color: #fff;
      width: 75%;
      margin-left: 5px;

      .content-rider {
        display: flex;
        align-items: center;
        margin: 10px 0;

        .content-rider-label {
          width: 30%;
        }

        .content-rider-value {
          //width: 50%;
        }
      }

      .content-rider-button-detail {
        margin-top: 20px;
        padding: 10px;

        button {
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 14px;
          background-color: #fff403;
          border-radius: 5px;
          color: black;
          border: 2px #fff403 solid;


          p {
            margin: 0;
          }
        }
      }
    }
  }
}

#detail-rider-page {
  .content {
    margin: 10px 20px
  }

  .button {
    display: flex;
    justify-content: center;

    .btn-update {
      background-color: #0f74f8;
      border-color: #0f74f8;
      color: #fff;
      border-radius: 5px;
      margin: 20px 20px;
      padding: 10px 15px;
    }
  }

  .image-account {
    display: flex;
    justify-content: center;
    margin: 10px 0;

    img {
      background-color: white;
      height: 120px;
      width: 120px;
      border-radius: 15px;
      border: 1px black solid;
    }
  }
}
