.activation-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  p {
    text-align: center;
  }

  .image {
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 250px;
    }
  }

  .failed {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .contact {
      display: flex;
      align-items: center;
      justify-content: center;
      a {
        margin-left: 10px;
        text-decoration: none;
        color: green;
      }
    }
  }
}
