.registration {
  display: flex;
  flex-direction: column;
  width: 100%;
  //align-items: center;
  //justify-content: space-between;

  .header {
    text-align: center;
    text-decoration: underline;
    margin-bottom: 10px;
  }

  .image-size {
    display: flex;
    justify-content: center;
    margin: 10px 0;

    img {
      background-color: white;
      height: 150px;
      width: 150px;
      border-radius: 5px;
      border: 1px black solid;
    }
  }

  .text-optional {
    margin: 5px 25px;
  }

  .form-complete {
    text-align: center;
    margin: 20px 0;

    img {
      height: 200px;
      width: 200px;
    }
  }

  .image-account {
    display: flex;
    justify-content: center;
    margin: 10px 0;

    img {
      background-color: white;
      height: 120px;
      width: 120px;
      border-radius: 15px;
      border: 1px black solid;
    }
  }
}

.button-finish {
  display: flex;
  align-items: center;
  justify-content: center;

  .button-reset {
    background: rgb(255, 244, 3);
    margin: 10px;
    font-weight: bolder;
    border-radius: 10px;
    border: 1px solid blue;
    padding: 10px
  }
}

.fixed-bottom {
  //position: fixed;
  //bottom: 70px;
  width: 100%;
  //padding: 10px; /* Padding opsional */
  //box-shadow: 0 -2px 5px rgba(0,0,0,0.1); /* Bayangan opsional */
  text-align: center; /* Penyelarasan teks opsional */

  .button {
    background: #0f74f8;
    margin: 10px;
    color: white;
    font-weight: bolder;
    border-radius: 10px;
    border: none;
    padding: 10px;
  }

  .button-disabled {
    background: rgba(173, 173, 173, 0.75);
    margin: 10px;
    color: white;
    font-weight: bolder;
    border-radius: 10px;
    border: none;
    padding: 10px
  }
}

.step-one {
  .header {
    text-align: center;
    text-decoration: underline;
    margin-bottom: 10px;
  }

  .content {
    margin: 0 10px;
  }
}

.step-two {
  margin-bottom: 50px;

  .content {
    margin: 0 10px;

  }
}

.step-three {
  .content {
    margin: 0 10px;
  }
}

.step-four {
  .content {
    margin: 0 10px;

    .info {
      display: flex;
      flex-direction: column;
      justify-content: center;
