.search {
  width: 50%;
  margin: 10px;
}

.member-protect {
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  button {
    margin: 10px;
    padding: 3px 10px;
    background-color: #002bb0;
    border-radius: 30px;
    color: white;
  }

  .watermark {
    position: fixed;
    bottom: 50px;
    //right: 5px;
    opacity: 0.5;
    //z-index: 99;
    color: rgba(173, 173, 173, 0.75);
  }

  .member-card {
    background-image: url('../../../../assets/image/MemberCard.png');
    background-size: cover;
    width: 400px;
    height: 250px;

    .content {
      display: flex;
      position: relative;
      justify-content: space-around;

      .member-name {
        margin: 60px 0 5px 5px;
        width: 240px;
        padding: 10px 0 5px 0;
        display: flex;
        align-items: flex-end;

        p {
          margin: 0;
          font-family: Comic, sans-serif;
          font-size: 22px;
          font-weight: bolder;
          text-decoration: underline;
        }
      }

      .member-image {
        margin-top: 35px;
        border: 2px #1313ff solid;
        border-radius: 50%;
        width: 110px;
        height: 110px;
        overflow: hidden;
        margin-right: 10px;
        display: flex;
        justify-content: center;

        .circle-image {
          border-radius: 50%;
          object-fit: cover;
          width: 100%;
          height: 100%;
          background-color: rgba(173, 173, 173, 0.75);

          img {
            height: 100%;
            width: 100%;
          }
        }
      }

      .member-id {
        position: absolute;
        margin-left: -215px;
        top: 65px;

        p {
          color: white;
          font-family: Comic, sans-serif;
          font-size: 20px;
          font-weight: bold;
        }
      }

      .label-category {
        position: absolute;
        margin-left: 310px;
        margin-right: 5px;
        color: white;
        font-style: italic;
        top: 30px;
      }

      .member-category {
        position: relative;
        width: 240px;
        margin-left: 145px;
        top: 50px;
        display: flex;
        justify-content: flex-end;

        p {
          margin: 0 10px 0 0;
          color: white;
          font-family: Comic, sans-serif;
          font-size: 25px;
          font-weight: bold;
        }
      }
    }
  }
}

.order-jersey {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  .image-size {
    display: flex;
    justify-content: center;
    margin: 10px 0;

    img {
      background-color: white;
      height: 150px;
      width: 150px;
      border-radius: 5px;
      border: 1px black solid;
    }
  }

  .button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;

    .order-jersey-button {
      background-color: #fff403;
      border: 1px solid #0f74f8;
      border-radius: 5px;
      padding: 5px 10px;
      cursor: pointer;
      color: black;
    }
  }
}
