.account {
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .account-content {
    width: 350px;
    margin: 15px;

    .image-account {
      display: flex;
      justify-content: center;

      img {
        background-color: white;
        height: 150px;
        width: 150px;
        border-radius: 100%;
        border: 1px black solid;
      }
    }
  }
}
