.event-header {
  margin: 10px;
  text-align: center;
  font-weight: bolder;
  font-size: 25px;
  font-family: 'Qureah', 'Fira Sans', fantasy;
}

.banner {
  display: flex;
  justify-content: center;
}

.event-pagination {
  display: flex;
  justify-content: center;
}

.event-banner {
  width: 50%;
  height: 200px;
  margin: 10px;
  background-color: rgb(251, 251, 33);
  display: flex;
  justify-content: left;
  border-radius: 10px;
  border: 3px #1313ff solid;

  .event-banner-image {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      margin: 0 10px;
      border-radius: 15px;
      @include dimension(150px, 150px);
    }
  }

  .event-banner-note {
    padding: 0 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .event-title {
      font-size: medium;
      font-weight: bolder;
      margin: 0.5rem;
    }

    .event-date {
      font-size: medium;
      margin: 0 0.5rem;
    }

    .event-fee {
      padding-top: 10px;
      font-weight: bolder;
      font-style: italic;
      margin: 0 0.5rem;

      .fee {
        background-color: #0f74f8;
        border-radius: 5px;
        padding: 5px;
        color: #fff403;
        font-size: medium;
        margin: 0.5rem;
      }
    }

    .event-button {
      margin: 15px;

      button {
        background-color: #0f74f8;
        color: white;
        border: 2px;
        border-radius: 15px;
        padding: 5px 10px;
        font-weight: bolder;
      }

      button:hover {
        cursor: pointer;
        background-color: rgba(15, 116, 248, 0);
        border: 2px solid #0f74f8;
        color: black;
      }
    }
  }

  @media (max-width: 767px) {
    height: auto;
    width: 95%;
    //flex-direction: row;

    img {
      margin: 0;
      @include dimension(80%, auto);
    }

    .event-banner-note {
      padding: 10px 0;

      .event-title {
        text-align: center;
        font-size: 16px;
        font-weight: bold;
      }

      .event-date {
        text-align: center;
        font-size: 12px;
        font-weight: bold;
      }

      .event-button {
        text-align: center;

        button {
          font-size: 12px;
        }
      }

      .event-fee {
        text-align: center;
        font-size: 16px;

        .fee {
          font-size: 10px;
        }
      }
    }
  }
}

.detail-event {
  .title {
    font-size: x-large;
    font-weight: bolder;
  }

  .image {
    margin-top: 10px;

    img {
      @include dimension(auto, 250px);
    }
  }

  .note {
    margin-top: 10px;
  }

  .button {
    text-align: center;
    margin-top: 10px;

    button {
      border: none;
      background-color: #0f74f8;
      padding: 10px;
      border-radius: 5px;
      color: white;
      font-weight: bolder;
      font-size: large;
    }

    button:hover {
      cursor: pointer;
    }

  }
}

#registration-event {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.icon-cancel {
  color: red;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.icon-cancel:hover {
  color: #1313ff;
}

.notify-closed {
  font-size: larger;
  color: red;
  padding: 10px;
}
