body {
  background-size: contain;
  background-image: url('../../../image/icon/background-web.svg');
  background-repeat: repeat;
  min-height: 100vh;
  font-family: 'Qureah', sans-serif;
}

#navbar {
  //height: 25px;
  background-image: url("../../../image/icon/header.svg");
  background-size: cover;
  margin-bottom: 25px;

  #navbar-content {
    padding: 0;
    height: 5px;
    #logo {
      img {
        height: 35px;
      }
    }
  }
}

.container {
  max-width: 1440px;
  @include mx-auto;
  @include padding-x(0.75rem, 0.75rem);

  @include devices(xl) {
    max-width: 1280px;
  }

  @include devices(lg) {
    max-width: 1024px;
  }

  @include devices(md) {
    max-width: 768px;
  }

  @include devices(sm) {
    max-width: 640px;
  }

  @include devices(xs) {
    width: 100%;
  }
}

.link {
  color: #0f74f8;
  text-decoration: underline;
}

.link:hover {
  cursor: pointer;
}

#image-navbar {
  display: flex;
  justify-content: center;
  align-items: center;

  img {
    height: 50px;
    width: 50px;
    border-radius: 100px;
  }

  .badge {
    background-color: rgb(0, 255, 0);
    padding: 5px;
    border-radius: 15px;
    margin: 10px;
    font-family: Comic, serif;
    font-weight: bold;
    animation: blink 1s linear infinite
  }
}

.image-navbar:hover {
  cursor: pointer;
}

@keyframes blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
